import React from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import { withStyles } from '@material-ui/core/styles';

import FlatList from '../components/FlatList';
import Spinner from '../components/Spinner';
import PostListItem from '../components/PostListItem';
import SideBar from '../components/SideBar';
import PageContent from '../components/PageContent';
import { Grid } from '@material-ui/core';

import { Posts } from '../api';

function mapStateToProps(state) {
  return state;
}

const styles = theme => ({
  root: {
    display: 'flex',
    flexDirection: 'row'
  },
  pageContent: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'space-around',
    overflow: 'hidden',
    backgroundColor: theme.palette.background.paper,
  }
});

class PostList extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      loading: false,
      posts: []
    }

  }
  
  componentWillMount() {
    this.getPosts(this.props.match.params.category);
  }

  componentWillReceiveProps(nextProps) {
    this.getPosts(nextProps.match.params.category);
  }

  componentWillUnmount() {
    this.posts.unsubscribe();
  }

  getPosts = (category) => {
    this.setState({ loading: true });
    this.posts = Posts.getByCategory(category)
                      .subscribe(posts => {
                        this.setState({
                          posts,
                          loading: false
                        });
                      });
  }

  render() {
    const { loading, posts } = this.state;
    const { classes } = this.props;

    return (
      <div className={classes.root}>
        <SideBar />
        <PageContent>
          <Grid 
            container 
            spacing={4} 
            direction="row">
            <FlatList 
              items={posts}
              isLoading={loading}
              renderLoading={ <Spinner /> }
              renderEmpty={ () => <div>There are no posts in this category..</div>}
              renderItem={ post => <PostListItem item={post} key={post.id} /> }
            />
          </Grid>
        </PageContent>
      </div>
    );
  }
}

PostList.propTypes = {
  classes: PropTypes.object.isRequired,
};

const styledComponent = withStyles(styles)(PostList);
export default connect(mapStateToProps, {})(styledComponent);